@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.sobre-mi {

    

    h3 {
        font-family: v.$fuente_principal;
        
    }
}

.tecnologias {
    display: flex;
    flex-wrap: wrap;
    img {
        height: 3rem;
        width: auto;
        margin-bottom: 1rem;
    }
}

.menos-no {
    @include m.solo-telefono {
        display: none;
    }
}



.block {
    display: block;
}

.mas-no {
    @include m.tablet {
        display: none;
    }
}
.imagen-mia-tecnologia.imagen-principal {
    height: 35rem;
}

.left-0 {
    left: 0;
}