@use 'variables' as v;
@use 'mixins' as m;
@use "sass:math";

.seccion {
    margin-top: v.$separacion * 2!important;
}

.seccion-uno {
    margin-top: 8rem;
}

.margin-top {
    margin-top: 8rem;

}
.contenido-centrado {
    max-width: 80rem;
}

.text-left {
    text-align: left;
}

.text-center {
    text-align: center;
}

.alinear-derecha {
    display: flex;
    justify-content: flex-end;
}

.w-100 {
    width: 100%;
}
.flex-centrar {
    display: flex;
    justify-content: center;
    align-items: center;
}
.displayNone {
    display: none;
}

.dos-columnas {
    @include m.grid(2, 2rem);
}

.dos-columnas-responsive {
    @include m.tablet {
        @include m.grid(2, 2rem);
    
    }
}

.no-margin {
    margin: 0;
}

.margen-derecha {
    &:not(:last-of-type) {
        margin-right: 2rem;

    }
}



.b-r-1 {
    border-radius: 1rem;
    overflow: hidden;
}


.no-margin-bottom {
    margin-bottom: 0;
}

.hidden {
    overflow: hidden
}

.margin-auto {
    margin: auto;
}

.m-l-r {
    margin-left: auto;
}


.grid-tres-columnas-responsive {
    @include m.telefono {
        @include m.grid(2, 2rem);
    }
    @include m.tablet {
        @include m.grid(3, 2rem);
        
    }
}

.svg-titulos {
    &::before {
        background: url(/build/img/svg-titulo.svg) center/contain no-repeat;
        content: "";
        position: absolute;
        height: 3rem;
        width: 19rem;
        /* left: 0; */
        bottom: -1.5rem;
        z-index: -1;
        transition: v.$tra;
        opacity: 1;
    }
}


