body {
  font-family: "Poppins", sans-serif;
  font-size: 1.6rem;
  line-height: 1.5;
  color: #34343c;
  transition: 0.8s ease-in-out;
}

::selection {
  background-color: #9cffa2;
  color: #34343c;
}

p {
  color: #34343c;
  font-size: 1.4rem;
  transition: 0.35s ease-in-out;
}

.contenedor {
  width: 90%;
  max-width: 100rem;
  margin: 0 auto;
}

a {
  text-decoration: none;
  font-size: 1.5rem;
  color: #34343c;
  font-weight: 700;
}

img,
picture {
  width: 100%;
  display: block;
  pointer-events: none;
}
img::selection,
picture::selection {
  background-color: transparent;
}

h1, h2, h3 {
  font-family: "Abril Fatface", cursive;
  margin: 3rem 0;
  font-weight: 800;
  color: #34343c;
  position: relative;
  transition: 0.35s ease-in-out;
}

h1 {
  font-size: 5rem;
}

h2 {
  font-size: 2.7rem;
}

h3 {
  font-size: 2rem;
  font-weight: 700;
  margin-bottom: 1rem;
}

h4 {
  font-size: 2rem;
  margin: 1.5rem 0;
  transition: 0.35s ease-in-out;
}

::-webkit-scrollbar {
  background-color: #FFFFFF;
}

::-webkit-scrollbar-thumb {
  background-color: #cfcfcf;
}

.seccion {
  margin-top: 12rem !important;
}

.seccion-uno {
  margin-top: 8rem;
}

.margin-top {
  margin-top: 8rem;
}

