.header .barra-nav {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  z-index: 100;
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: #FFFFFF;
  height: 8rem;
  transition: 0.35s ease-in-out;
}
.header .barra-nav-hijo {
  display: flex;
  justify-content: space-between;
  flex-direction: row;
}
@media (min-width: 768px) {
  .header .barra-nav-hijo {
    justify-content: space-between;
  }
}
.header .logo {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.header .logo img {
  height: 3rem;
  transition: 0.35s ease-in-out;
}
@media (min-width: 768px) {
  .header .logo img {
    display: flex;
    justify-content: left;
  }
}
.header .navegacion-iconos-padre {
  justify-content: right;
}
.header .nav {
  display: flex;
  justify-content: center;
  align-items: center;
}
@media (max-width: 767px) {
  .header .nav {
    display: flex;
    height: calc(100vh - 8rem);
    flex-direction: column;
    align-items: center;
    transition: 0.8s ease-in-out;
    transform: translateX(-100%);
    visibility: hidden;
    opacity: 0;
    position: fixed;
    top: 8rem;
    left: 0;
    right: 25%;
    z-index: 100;
    padding: 2rem;
  }
  .header .nav a {
    color: #34343c;
    display: flex;
    width: 100%;
    justify-content: center;
    position: relative;
  }
  .header .muestra {
    transform: translateX(0);
    background-color: #FFFFFF;
    visibility: visible;
    opacity: 1;
  }
}
.header .enlace-navegacion {
  position: relative;
  width: 50%;
  display: flex;
  justify-content: center;
}
@media (min-width: 768px) {
  .header .enlace-navegacion {
    width: auto;
    display: block;
    justify-content: end;
  }
}
.header .nav-hijos {
  color: #34343c;
  transition: 0.35s ease-in-out;
  padding: 2rem 0;
  text-align: center;
  cursor: pointer;
  font-weight: 400;
  position: relative;
}
.header .nav-hijos::after {
  content: "";
  position: absolute;
  bottom: 1rem;
  left: 0;
  width: 100%;
  height: 2px;
  transform: scaleX(0);
  transition: transform 0.35s ease-in-out;
  transform-origin: bottom right;
  background-color: #9cffa2;
}
.header .nav-hijos:hover::after {
  transform-origin: bottom left;
  transform: scaleX(1);
}
@media (min-width: 768px) {
  .header .nav-hijos {
    text-align: right;
    margin-right: 2rem;
  }
}

.iconos-nav {
