}
.formulario textarea {
  height: 20rem;
  resize: none;
}
.formulario .form-input {
  display: grid;
  margin-bottom: 2rem;
}
.formulario .form-input .sitio-web-si-no {
  display: flex;
}
.formulario .form-input .sitio-web-si-no .check {
  display: flex;
  justify-content: left;
  align-items: center;
}
.formulario .form-input .sitio-web-si-no .check label {
  margin: 0;
}
.formulario .form-input .sitio-web-si-no .check input {
  margin: 0 3rem 0 1rem;
  height: 2.5rem;
  width: 2.5rem;
  transition: none;
}
.formulario .form-input .sitio-web-si-no .check input:checked {
  filter: hue-rotate(247deg) opacity(0.6);
}
.formulario .form-input .validacion-error {
  height: 1rem;
  visibility: hidden;
  opacity: 0;
  transition: 0.35s ease-in-out;
}
.formulario .form-input .validacion-error p {
  color: #e63636;
  margin-bottom: 0;
}
.formulario .form-input .validacion-error p i {
