  height: 1.5rem;
  width: 1.5rem;
  clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
  background-color: #e6da36;
}
.estrellas span:not(:last-of-type) {
  margin-right: 1rem;
}
.estrellas .estrella-mitad {
  clip-path: polygon(50% 0%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
}

.sin-miedo-exito {
  min-height: 40rem;
  height: 50vh;
  max-height: 50rem;
}
.sin-miedo-exito h3 {
  font-size: 5rem;
}
.sin-miedo-exito .exito {
  margin: auto;
}

.entrada {
  height: 100%;
  min-height: auto;
  max-height: 100%;
  padding: 2rem;
  background-attachment: scroll;
  background-position: center center;
}

.mini-seccion {
  margin-top: 3rem;
}

@media (min-width: 768px) {
  .grid-proyectos {
    display: grid;
    grid-template-columns: 60% 1fr;
    column-gap: 2rem;
    row-gap: 2rem;
    gap: 2rem;
  }
  .grid-proyectos:nth-child(2n) {
    grid-template-columns: 1fr 60%;
  }
  .grid-proyectos:nth-child(2n) .datos-proyectos {
    grid-column: 1/2;
    grid-row: 1/2;
  }
}

.contenedor-sobre-mi {
  width: 90%;
  max-width: 80rem;
  margin: 0 auto;
}
@media (min-width: 768px) {
  .contenedor-sobre-mi {
    width: 80%;
  }
}

.estudios .dos-columnas img {
  width: 50%;
}

.ventanaContacto {
  position: fixed;
  top: 8rem;
  width: 100%;
  height: calc(100vh - 8rem);
  background-color: #FFFFFF;
  padding: 3rem;
  transition: 0.8s ease-in-out;
  visibility: hidden;
  opacity: 0;
  transition: 0.35s ease-in-out;
}
.ventanaContacto .height {
  height: calc(100% - 8rem);
}
.ventanaContacto span {
  font-weight: 700;
}
.ventanaContacto .redes-sociales {
  display: flex;
}
.ventanaContacto .redes-sociales a:not(:last-of-type) {
  margin-right: 1rem;
}
.ventanaContacto .redes-sociales a i {
  display: flex;
  justify-content: center;
  align-items: center;
  font-size: 2rem;
  transition: 0.35s ease-in-out;
  color: #34343c;
  height: 5rem;
  width: 5rem;
  background-color: #FFFFFF;
  border-radius: 50%;
  cursor: pointer;
}
.ventanaContacto .redes-sociales a i:hover {
  color: #9cffa2;
  background-color: #34343c;
}

.mostrarVentana {
  visibility: visible;
  opacity: 1;
}

.footer {
  height: 8rem;
  width: 100%;
}

