@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.ventanaContacto {
    position: fixed;
    top: 8rem;
    width: 100%;
    height: calc(100vh - 8rem);
    background-color: v.$blanco;
    padding: 3rem;
    transition: v.$tra-menu;
    visibility: hidden;
    opacity: 0;
    transition: v.$tra;

    .height {
        height: calc(100% - 8rem);
    }
    span {
        font-weight: v.$semi-bold;
    }

    .redes-sociales {

        display: flex;

        a {
            &:not(:last-of-type) {
                margin-right: 1rem;
            }

            i {
                @include m.flex-centrar;
                font-size: 2rem;
                transition: v.$tra;
                color: v.$negro;
                height: 5rem;
                width: 5rem;
                background-color: v.$blanco;
                border-radius: 50%;
                cursor: pointer;
    
                
                &:hover {
                    color: v.$pri;
                    background-color: v.$negro;
    
                }
            }
        }
        
    }
}

.mostrarVentana {
    
    visibility: visible;
    opacity: 1;
}