@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";



.span {
    display:block;
    background-color: v.$negro;
    border-radius:2px;
    transition: v.$tra;
}
  
  #menu-toggle {
    width:10rem;
    height:10rem;
    // margin:50px auto;
    position:relative;
    position:relative;
    cursor:pointer;
    border-radius:5px;
    // &:hover {
    //     background:rgba(255,255,255,.8);
    //   }
    #hamburger {
      position:absolute;
      height:100%;
      width:100%;
      span {

        
        width:6rem;
        height:4px;
        position:relative;
        top:2.4rem;
        left:2rem;
        margin:1rem 0;
        &:nth-child(1){
          transition-delay:.5s;
        }
        &:nth-child(2){
          transition-delay:.625s;
        }
        &:nth-child(3){
          transition-delay:.75s;
        }
      }
    }
    #cross {
      position:absolute;
      height:100%;
      width:100%;
      transform:rotate(45deg) ;
      span{
        &:nth-child(1){
          height:0%;
          width:4px;
          position:absolute;
          top:10%;
          left:4.8rem;
          transition-delay:0s;
        }
        &:nth-child(2){
          width:0%;
          height:4px;
          position:absolute;
          left:10%;
          top:4.8rem;
          transition-delay:.25s;
        }
      }
    }
  }
  
  #menu-toggle.open {
    #hamburger {
      span {
        width:0%;
        &:nth-child(1){
          transition-delay:0s;
        }
        &:nth-child(2){
          transition-delay:.125s;
        }
        &:nth-child(3){
          transition-delay:.25s;
        }
      }
    }
    #cross {
      span {
        &:nth-child(1){
          height:80%;
          transition-delay:.625s;
        }
      }
      span {
        &:nth-child(2){
          width:80%;
          transition-delay:.375s;
        }
      }    
    }
  }

  @include m.tablet {
    #menu-toggle {
      display: none;
    }
  }