@use 'variables' as v;
@use 'mixins' as m;
@use "sass:math";

html {
    font-size: 62.5%;
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}
body {
    font-family: v.$fuente_principal;
    font-size: 1.6rem;
    line-height: 1.5;
    color: v.$negro;
    transition: v.$tra-menu;
}


::selection {
    background-color: v.$pri;
    color: v.$negro;
}
p {
    color: v.$negro;
    font-size: 1.4rem;
    transition: v.$tra;

}
.contenedor {
    width: 90%;
    max-width: 100rem;
    margin: 0 auto;
}

a {
    text-decoration: none;
    font-size: v.$t-2;
    color: v.$negro;
    font-weight: v.$semi-bold;
}

img,
picture {
    width: 100%;
    display: block;
    pointer-events: none;
    &::selection {
        background-color: transparent;

    }
}

h1, h2, h3 {
    font-family: v.$fuente_secundaria;
    margin: math.div(v.$separacion, 2) 0;
    font-weight: v.$bold;
    // text-align: center;
    color: v.$negro;
    position: relative;
    transition: v.$tra;
}

h1 {
    font-size: 5rem;
}
h2 {
    font-size: 2.7rem;
    
}
h3 {
    font-size: 2rem;
    font-weight: v.$semi-bold;
    margin-bottom: 1rem;
}
h4 {
    font-size: 2rem;
    margin: 1.5rem 0;
    transition: v.$tra;

}
