@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.fondo-gradiente {
    background: linear-gradient(180deg, v.$blanco 0%, v.$ter 100%);

    
    height: calc(100vh - 8rem);
    min-height: 40rem;
    background-position: center 8rem;
    background-attachment: fixed;
    background-repeat: no-repeat;
    transition: v.$tra;
    
}

.bienvenida {

    .datos-principales {

        
        p {
            font-size: 2.5rem;
            span {
                font-weight: v.$bold;
            }
        }
    }

    
}

.imagen-principal {
    
    height: 35rem;
    width: 100%;
    position: relative;
    z-index: 1;

    // }
    img {
            /* bottom: 0; */
    -o-object-fit: contain;
    object-fit: contain;
    /* position: absolute; */
    /* right: 0; */
    width: 30rem;
    margin: auto;
    }
    
}

.rectangulo-imagen {
    border-radius: 1rem;
    bottom: 16rem;
    height: 16rem;
    z-index: -1;
    // max-width: 35rem;
    position: relative;
    right: 0;
    width: 100%;
    background-color: v.$pri;
    transition: v.$tra;

}