@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.testimoniales {
    margin-left: 5%;
    width: 100%;
    display: flex;
    overflow: auto;
    padding-right: 5%;
    position: relative;

    
    @include m.tablet {
        ::-webkit-scrollbar {
            // width: 2rem;
            // height: 1rem;
            background-color: v.$blanco;
        
        }
        
        ::-webkit-scrollbar-thumb {
            background-color: v.$gris-oscuro;
            // width: 2rem;
            // border-radius: 1rem;
            // height: 10rem;
            // box-shadow: 0 0 6px 2px v.$gris-claro;
        }
    }
    


    p {
        width: 75vw;
        margin-right: 2rem;
        @include m.tablet {
            width: 40rem;
            
        }
    }
    
}

.estrellas {
    display: flex;
    
    span {
        height: 1.5rem;
        width: 1.5rem;
        clip-path: polygon(50% 0%, 61% 35%, 98% 35%, 68% 57%, 79% 91%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
        background-color: v.$amarillo;
        &:not(:last-of-type) {
            margin-right: 1rem;
    
        }
    }

    .estrella-mitad {
        clip-path: polygon(50% 0%, 50% 70%, 21% 91%, 32% 57%, 2% 35%, 39% 35%);
    
    }
} 

