/** Media Queries **/

@use 'variables' as v;
@use "sass:math";

@mixin telefono {
    @media (min-width: v.$telefono) {
        @content;
    }
}

@mixin solo-telefono {
    @media (max-width: 767px) {
        @content;
    }
}

@mixin tablet {
    @media (min-width: v.$tablet) {
        @content;
    }
}
  
@mixin desktop {
    @media (min-width: v.$desktop) {
        @content;
    }
}
@mixin grid($columnas, $espaciado ) {
    display: grid;
    column-gap: $espaciado;
    row-gap: $espaciado;
    grid-template-columns: repeat($columnas, 1fr);
}

@mixin boton($bgColor, $display ) {
    background-color: $bgColor;
    color: v.$negro;
    font-weight: v.$bold;
    text-decoration: none;
    padding: 2rem 4.5rem;
    text-align: center;
    margin: 2rem 0 ;
    display: $display;
    border: none;
    border-radius: 4rem;
    transition: v.$tra;
    white-space: nowrap;
    text-overflow: ellipsis;
    box-shadow: 0px 0px 20px 0px lighten(v.$pri, 5);
   
    &:hover {
        background-color: darken($bgColor, 10);
        cursor: pointer;
    }
}

@mixin flex-centrar {
    display: flex;
    justify-content: center;
    align-items: center;
}