@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.btn-modo-oscuro{
    position: relative;
    margin-right: 2rem;
    cursor: pointer;
}

.sun, .moon{
    font-size: 5rem;
    width: fit-content;
    height: fit-content;
    color: v.$negro;
    // opacity: .7;
}

.moon{
    position: absolute;
    inset: 0;
    color: v.$blanco;
    // opacity: 1;

}

