.moon-logo{
    opacity: 0;
    visibility: hidden;
    transform: translateY(20%) rotateZ(50deg);
    transition: all 1s ease-out;
}
.sun-logo{
    opacity: 1;
    visibility: visible;
    transform: translateY(0) rotateZ(0deg);
    transition: all 1s ease-out;
}

.animate-sun{
    opacity: 0;
    visibility: hidden;
    transform: translateY(20%) rotateZ(100deg);
    // color: v.$negro;
}

.animate-moon{
    opacity: .7;
    visibility: visible;
    transform: translateY(0%) rotateZ(0deg);
    
}



