@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.contenedor-formulario {
    width: 90%;
    max-width: 80rem;
    margin: 0 auto;
    @include m.tablet {
        width: 80%;
    }
}


.formulario {
    fieldset {
        padding: 0;
        border: none;
    }

    label {
        font-size: 1.4rem;
        color: v.$negro;
        margin-bottom: 1.4rem;
        transition: v.$tra;

        span {
            margin-left: 1rem;
            color: v.$rojo;
        }
    }

    input, textarea {
        border: 1px solid v.$negro;
        transition: v.$tra;
        padding: 1rem 2rem;
        outline: none;
        width: 100%;
        font-size: 1.6rem;
        color: v.$negro;
        
        &::placeholder {
            color: v.$gris-oscuro;
        }

        &:focus {
            border-color: v.$c-logo;

        }

        

        &[type="submit"] {
            border: none;
            padding: 2rem 4.5rem;
            width: auto;

        }

        
    }

    textarea {
        height: 20rem;
        resize: none;
    }

    .form-input {
        display: grid;
        margin-bottom: 2rem;

        .sitio-web-si-no {

            display: flex;
            .check {
                display: flex;
                justify-content: left;
                align-items: center;
                label {
                    margin: 0;
                }
                input {
                    // margin-bottom: 1.4rem;
                    // width: auto;
                    margin: 0 3rem 0 1rem;
                    height: 2.5rem;
                    width: 2.5rem;
                    transition: none;


                    &:checked {
                        filter: hue-rotate(247deg) opacity(0.6);

                    }
                }
            }
        }



        

        .validacion-error {


            // height: auto;
            height: 1rem;
            visibility: hidden;
            opacity: 0;
            transition: v.$tra;
            p {
                color: v.$rojo;
                margin-bottom: 0;

                i {
                    margin-right: 1rem;
                }
            }
        }

        // #nombre, #apellido, #telefono, #correo, #negocio, #sitio, #presupuesto, #mensaje {
        //     &:invalid {
        //         border-color: v.$rojo;
        //     }
        // }

        // #nombre {
        //     &:invalid {
        //         &.validacion-error.nombre {
        //             opacity: 1;
        //             visibility: visible;
        //         }
        //     }
        // }
        

    }
}