@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.entrada {
    height: 100%;
    min-height: auto;
    max-height: 100%;
    padding: 2rem;
    background-attachment: scroll;
    background-position: center center;
}

.mini-seccion {
    margin-top: 3rem;
}

.grid-proyectos {

    @include m.tablet {
        display: grid;
        // grid-template-columns: repeat(75%, 25%);
        // @include m.grid(2, 2rem);
        // @include m.grid(2, 2rem);
        grid-template-columns: 60% 1fr;
        column-gap: 2rem;
        row-gap: 2rem;
        gap: 2rem;

        &:nth-child(2n) {
        grid-template-columns: 1fr 60%;

            .datos-proyectos {
                grid-column: 1/2;
                grid-row: 1 / 2;
        
            
        
            }
        }
    }
    
    

    
}
