@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";



.header {
    
    .barra-nav {  
        position: fixed;
        top: 0;
        left: 0;
        right: 0;
        z-index: 100;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: v.$blanco;
        height: 8rem;
        transition: v.$tra;
        // box-shadow: 0 2px 10px v.$gris-claro;
        
      
       
    }
    .barra-nav-hijo {
        display: flex;
        justify-content: space-between;
       
        flex-direction: row;
        @include m.tablet {
           
            justify-content: space-between;
        }
        
    }
   
    .logo {
        display: flex;
        justify-content: space-between;
        align-items: center;
        width: 100%;
        img {
            height: 3rem;
            transition: v.$tra;
    
            
            @include m.tablet {
                display: flex;
                justify-content: left;
            }
        }
      
    }
    
.navegacion-iconos-padre {
    justify-content: right;
}
    

    .nav {
        display: flex;
        justify-content: center;
        align-items: center;


    }
   
@include m.solo-telefono {

    .nav {

        display: flex;
        height: calc(100vh - 8rem);
        flex-direction: column;
        align-items: center;
        // box-shadow: inset 0px 2px 10px 0px v.$gris-claro;
        transition: v.$tra-menu;
        // transition: visibility v.$tra-menu, 
        // opacity v.$tra-menu,
        // transform v.$tra-menu;
        // height v.$tra-menu, 
        // background-color v.$tra-menu,  
        // padding-top v.$tra-menu,
        // padding-bottom v.$tra-menu, 
        transform: translateX(-100%);

        visibility: hidden;
        opacity: 0;
        position: fixed;
        top: 8rem;
        left: 0;
        right: 25%;
        z-index: 100;
        padding: 2rem;
        a {
            color: v.$negro;
            display: flex;
            
            width: 100%;
            justify-content: center;
            position: relative;

            
        }
        
    }

    .muestra {
        transform: translateX(0);
        background-color: v.$blanco;
        visibility: visible;
        opacity: 1;

    }
}


.enlace-navegacion {
    position: relative;
    width: 50%;
    display: flex;
    justify-content: center;

    @include m.tablet {
        width: auto;
        display: block;
        justify-content: end;
    }
