    // }

    

    
} 

.nav-hijos {
    color: v.$negro;
    transition: v.$tra;
    padding: 2rem 0;
    text-align: center;
    cursor: pointer;
    font-weight: v.$regular;

    position: relative;
    &::after {
        content: '';
        position: absolute;
        bottom: 1rem;
        left: 0;
        width: 100%;
        height: 2px;
        transform: scaleX(0);
        transition: transform v.$tra;
        transform-origin: bottom right;
        background-color: v.$pri;
    }

    &:hover::after {
        transform-origin: bottom left;
        transform: scaleX(1);
    }

    @include m.tablet {
        text-align: right;
        margin-right: 2rem;

        // &:hover {
        // box-shadow: 0 2px 0px v.$negro;
        // cursor: pointer;
        // }
        }

    }

    // @include m.tablet {
    //     flex-direction: row;
    //     align-items: center;
    //     padding: auto;
        
    // }
        
}

.iconos-nav {
    transform: scale(0.6) translateX(4rem);
    
}


.pantallaCompleta {
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(156, 255, 163, 0.4);
    // backdrop-filter: blur(0);
    opacity: 0;
    visibility: hidden;
    z-index: 10;
    color: transparent;
    transition: v.$tra-menu;
}



.activarPantallaNegra {
    opacity: 1;
    visibility: visible;
    // backdrop-filter: blur(1rem);

}

.fijar-body {
    overflow: hidden;
}



    

