@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.distinto-demas {

    .card {
        box-shadow: 0px 0px 10px 0px v.$gris-claro;
        padding: 2rem;
        margin-bottom: 2rem;
        transition: v.$tra;
        
        &:hover {
            box-shadow: 0px 0px 10px 0px darken(v.$gris-claro, 10);

        }

        @include m.tablet {
            margin: 0;
        }

        i {
            width: 5rem;
            height: 5rem;
            border-radius: 50%;
            color: v.$negro;
            background-color: v.$pri;
            margin: auto;
            transition: v.$tra;

            @include m.flex-centrar;
        }
        h3 {
            font-family: v.$fuente_principal;
            
        }

        // h3, p {
        //     text-align: left;
            
        // }

        p {
            @include m.tablet {
                text-align: center;
                    
                }
        }
    }
}