@use "base/variables" as v;
@use "base/mixins" as m;
@use "sass:math";

.dark {
    

    .barra-nav {
        background-color: v.$negro;
        
        img {
            filter: invert(1);
        }
    }

    .fondo-gradiente {
        
        background: linear-gradient(180deg, v.$negro, darken(v.$pri, 70));
        background-attachment: fixed;
        background-position: center 8rem;
        background-repeat: no-repeat;
    }

    .nav-hijos:after {
        background-color: v.$pri-o;
    }

    .span {
        background-color: v.$blanco;
        opacity: .7;
    }


    h1, h2, h3, h4 {
        color: darken(v.$blanco, 2);
    }

    p, a,
    .formulario label {
        color: v.$blanco;
        opacity: .7;
    }

    .boton-pri {
        background-color: v.$pri-o;
        color: v.$blanco;
        // box-shadow: 0px 0px 15px 0px darken(v.$pri, 10);
        box-shadow: none;
        &:hover {
            background-color: darken(v.$pri-o, 5);
            // box-shadow: 0px 0px 15px 0px darken(v.$pri, 50);

        }
    }

    .rectangulo-imagen {
        background-color: v.$pri-o;
    }

    .barra-nav a {
        color: v.$blanco;
        opacity: .7;
    }

    // @include m.tablet {
    //     .barra-nav a {
    //         color: v.$blanco;
    //         opacity: .7;
    //     }
    // }

    .muestra {
        background-color: v.$negro;
    }

    //body color
    background-color: v.$negro;
    //fin

    .svg-titulos {
        &::before {
            
            opacity: .3;
        }
    }

    .card {
        box-shadow: 0 0 15px 0 darken(v.$gris-oscuro, 20);

        i {
            background-color: v.$pri-o;
            color: v.$blanco;       
        }

        &:hover {
            box-shadow: 0 0 15px 0 darken(v.$gris-oscuro, 10);

        }
    }

    &::-webkit-scrollbar {
        // width: 2rem;
        // height: 1rem;
        background-color: lighten(v.$negro, 10);
    
    }
    
    &::-webkit-scrollbar-thumb {
        background-color: v.$gris-oscuro;
        // border-radius: 1rem;
        // height: 10rem;
        // box-shadow: 0 0 6px 2px v.$gris-claro inset;
    }


    ::-webkit-scrollbar {
        // width: 2rem;
        // height: 1rem;
        background-color: lighten(v.$negro, 10);
    
    }
    
    ::-webkit-scrollbar-thumb {
        background-color: v.$gris-oscuro;
        // border-radius: 1rem;
        // height: 10rem;
        // box-shadow: 0 0 6px 2px v.$gris-claro inset;
    }

    .formulario input, .formulario textarea {
        background-color: lighten(v.$negro, 10);
        color: v.$blanco;
        opacity: .7;

        &::placeholder{
            color: v.$blanco;
            opacity: .7;
        }
    }

    .formulario input[type=submit] {
        background-color: v.$pri-o;
        color: v.$blanco;
        // box-shadow: 0px 0px 15px 0px darken(v.$pri, 10);
        box-shadow: none;
        &:hover {
            background-color: darken(v.$pri-o, 5);
            // box-shadow: 0px 0px 15px 0px darken(v.$pri, 50);

        }
    }

    .ventanaContacto {
        background-color: v.$negro;
        .redes-sociales {
            color: v.$blanco;
            opacity: .7;
        }
    }
    

}